.section.about{
  position: absolute;
  top: 0;
  right: 100%;
  width: 100%;
  height: 100%;
  transition: 0.3s linear;
  display: flex;
}
.about-radio:checked + .section.about{
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  transition: 0.3s linear;
}

.about-radio{
  display: none;
}

.section.about{
  background-color: red;
}
